import React,{useState,useEffect} from 'react'
import {Card,Form,Button} from 'react-bootstrap'
import {auth,storage,firestore} from "../firebase/firebase"
import Firebase from 'firebase'
function Signup(argument) {
	// body...
	const [name,setName]=useState('')
	const [email,setEmail]=useState('')
	const [password,setPassword]=useState('')
	const [skill,setSkill]=useState('')
	const [about,setAbout]=useState('')
	const [imageAsFile, setImageAsFile] = useState('')
    const [imageAsUrl, setImageAsUrl] = useState('')
     const handleImageAsFile = (e) => {
      const image = e.target.files[0]
      setImageAsFile(imageFile => (image))
  } 
  const handleName=(event)=>{
  	setName(event.target.value);
  }
  const handlePassword=(event)=>{
  	setPassword(event.target.value)
  }
  const handleEmail=(event)=>{
  	setEmail(event.target.value)
  }
  const handleSkill=(event)=>{
  	setSkill(event.target.value)
  }
  const handleAbout=(event)=>{
  	setAbout(event.target.value)
  }
    const handleImageUpload=()=>{
            const uploadTask = storage.ref(`/user-photo/${imageAsFile.name}`).put(imageAsFile)
    //initiates the firebase side uploading 
    uploadTask.on('state_changed', 
    (snapShot) => {
      //takes a snap shot of the process as it is happening
      console.log(snapShot)
    }, (err) => {
      //catches the errors
      console.log(err)
    }, () => {
      // gets the functions from storage refences the image storage in firebase by the children
      // gets the download url then sets the image from firebase as the value for the imgUrl key:
      storage.ref('user-photo').child(imageAsFile.name).getDownloadURL()
       .then(fireBaseUrl => {
         setImageAsUrl(fireBaseUrl)
       })
       .catch((error)=>{
        console.log(error);
       })
       console.log(storage.ref('user-photo').child(imageAsFile.name).getDownloadURL())
      })
    }
    const handleSubmit=(event)=>{
    	event.preventDefault();
    	Firebase.auth().createUserWithEmailAndPassword(email,password)
    	.then(()=>{
        var user = Firebase.auth().currentUser;    
          user.updateProfile({
          displayName:name,
          photoURL:imageAsUrl
        })
        user.updateEmail(email)
        alert("Successful Sign Up")
      })
    	.catch((error)=>{
    		if(error.code=="auth/weak-password")
    			console.log("Password should be at least 6 characters")
    	})
      Firebase.auth().onAuthStateChanged((user)=>{
        const db=Firebase.firestore()
       const userRef=db.collection("users").doc(user.uid).set({
        skill:skill,
        about:about,
        uid:user.uid
       })
       .then(console.log("data saved"))
    })
    }
    
	return(<div>
		<Card>
		<Card.Title>Enter details here...</Card.Title>
		<Card.Body>
		<Form onSubmit={handleSubmit}>
		<Form.Group>
		<Form.Label>Enter Name:</Form.Label>
		<Form.Control type="text" defaultValue={name} onChange={handleName} required/>
		</Form.Group>
		<Form.Group>
		<Form.Label>Enter Email:</Form.Label>
		<Form.Control type="email" defaultValue={email} onChange={handleEmail} required/>
		</Form.Group>
		<Form.Group>
		<Form.Label>Enter Password:</Form.Label>
		<Form.Control type="password" defaultValue={password} onChange={handlePassword} required/>
		</Form.Group>
		<Form.Group>
		<Form.Label> Skill's:</Form.Label>
		<Form.Control type="text" defaultValue={skill} onChange={handleSkill}/>
		</Form.Group>
		<Form.Group>
		<Form.Label>About yourself:</Form.Label>
		<Form.Control type="text" defaultValue={about} onChange={handleAbout}/>
		</Form.Group>
		<Form.Group>
		  <Form.Control type="file"  onChange={handleImageAsFile}/>
          <Button type="button" variant="success" onClick={handleImageUpload}>Click to Upload Image</Button>
        </Form.Group>
        <Button type="submit" variant="danger">Submit Data</Button>
		</Form>
		</Card.Body>
		</Card>
		</div>)
}
export default Signup