import React,{useState,useEffect} from 'react'
import {auth,storage,firestore} from '../firebase/firebase'
import Firebase from 'firebase'
import {Card,Form,Button} from 'react-bootstrap'
import {Link,Redirect,Switch,Route} from 'react-router-dom'
import Introductionpage from "./introductionpage"
function Auth(props) {
	const [email,setEmail]=useState("")
	const [password,setPassword]=useState("")
	const handleEmail=(event)=>{
		setEmail(event.target.value)
	}
	const handlePassword=(event)=>{
		setPassword(event.target.value)
	}
	const handleGoogleAuth=()=>{
		const auth=new Firebase.auth.GoogleAuthProvider();
		Firebase.auth().signInWithPopup(auth)
		.then(
         alert("successful sign in.")
		   
		)
		.catch((error)=>{alert(error)})
	}
	const handleSubmit=(event)=>{
		 Firebase.auth().signInWithEmailAndPassword(email,password)
		 .then(	()=>{	 	
		 	return(
         alert("successful sign in.")
		    		    )
		 })
		 .catch((error)=>alert(error))
	}
	return(<div>
           <Card bg="light" variant="light">
           <Card.Title>Log In yourself here.</Card.Title>
           <Form onSubmit={handleSubmit}>
           <Form.Group controlId="email">
           <Form.Label>Email:</Form.Label>
           <Form.Control type="email" required defaultValue={email} onChange={handleEmail} placeholder="Enter your email."/>
           </Form.Group>
            <Form.Group controlId="password">
           <Form.Label>Password:</Form.Label>
           <Form.Control type="password" required defaultValue={password} onChange={handlePassword} placeholder="Enter your password."/>
           </Form.Group>
           <Button type="submit">Submit</Button>
           </Form>
           <Card.Footer>Or register here yourself<Link to="/signup"> Click Me!</Link>
           <br/>
           <Link to="/forgotPassword">Forgot Password</Link><br/>
           <Button variant="danger" type="button" onClick={handleGoogleAuth}>Login with Google</Button>
           </Card.Footer>
           </Card>
		</div>)
}
export default Auth