import React,{useState,useEffect} from 'react'
import Firebase from 'firebase'
import {auth,firestore,storage} from "../firebase/firebase"
import {Container,Image,Button,Row,Col,Form} from 'react-bootstrap'
import './profile.css'
import {Link} from 'react-router-dom'
function Profile(props) {
	const [user,setUser]=useState({})
	const [view,setView]=useState(false)
	const [photo,setPhoto]=useState('')
	const [name,setName]=useState('')
	const [email,setEmail]=useState('')
	const [imageAsFile, setImageAsFile] = useState('')
    const [imageAsUrl, setImageAsUrl] = useState('')
     const handleImageAsFile = (e) => {
      const image = e.target.files[0]
      setImageAsFile(imageFile => (image))
  } 
  const handlePhoto=()=>{
  	setView(true)
  }
  const handleImageUpload=()=>{
            const uploadTask = storage.ref(`/user-photo/${imageAsFile.name}`).put(imageAsFile)
    //initiates the firebase side uploading 
    uploadTask.on('state_changed', 
    (snapShot) => {
      //takes a snap shot of the process as it is happening
      console.log(snapShot)
    }, (err) => {
      //catches the errors
      console.log(err)
    }, () => {
      // gets the functions from storage refences the image storage in firebase by the children
      // gets the download url then sets the image from firebase as the value for the imgUrl key:
      storage.ref('user-photo').child(imageAsFile.name).getDownloadURL()
       .then(fireBaseUrl => {
         setImageAsUrl(fireBaseUrl)
       })
       .catch((error)=>{
        console.log(error);
       })
  
      })

	}
	const handleLogout=()=>{
		Firebase.auth().signOut()
		.then(()=>{console.log("sign-out successful")})
		.catch((error)=>{console.log(error)})
	}
  useEffect(()=>{
        var user=Firebase.auth().currentUser;
        if(user){
        	user.updateProfile({
        		photoURL:imageAsUrl
        	})
        	setView(false)
        }
  	Firebase.auth().onAuthStateChanged((user)=>{
  		if(user){
  			setPhoto(user.photoURL)
  			setName(user.displayName)
  			const db=Firebase.firestore();
  			db.collection("users").where("uid","==",user.uid).get()
  			.then(function (querySnapshot) {
    querySnapshot.forEach(function (doc) {
        console.log(doc.uid, ' => ', doc.data());
        setUser(doc.data());
    })})
  			console.log("user is logged in.")
  		}
  		else{
  			console.log("No user is logged in.")
  		}
  	})
  },[imageAsUrl])
	return(<div>
		<Container fluid>
		     <Row>
                <Image src={photo} roundedCircle/>
               {name}</Row>
               <Col className="list">
               <Row className="list-item" onClick={handlePhoto}>Change Profile-photo</Row>
                {
                  view?<div><Form.Control type="file"  onChange={handleImageAsFile}/>
                  <Button type="button" variant="success" onClick={handleImageUpload}>Click to Upload Image</Button>
                  </div>:null
                }
               <Row className="list-item">Your Blogs</Row>
               <Row className="list-item"><Link to="/account-update">Manage Account</Link></Row>
               <Row className="list-item" onClick={handleLogout}>Log Out</Row>
               </Col>
        </Container>
		</div>)
}
export default Profile