import React,{useState,useEffect} from 'react'
import {Form,Button} from 'react-bootstrap'
import Firebase from 'firebase'
import {auth} from '../firebase/firebase'
function ForgotPassword(props) {
	// body...
	const [email,setEmail]=useState("")
	const handleEmail=(event)=>{
		setEmail(event.target.value);
	}
	const handleSubmit=(event)=>{
		const auth=Firebase.auth();
		auth.sendPasswordResetEmail(email)
		.then(alert("Password reset email has been sent to a given email id"))
		.catch((error)=>alert(error))
	}
	return(<div>Forgot Password:
        <Form onSubmit={handleSubmit}>
        <Form.Group>
        <Form.Label>Enter your resgistered Email:</Form.Label>
        <Form.Control type="email" placeholder="registered email" defaultValue={email} onChange={handleEmail}/>
        <Button type="submit">Submit</Button>
        </Form.Group>
        </Form>
		</div>)
}
export default ForgotPassword