import React,{useState,useEffect} from 'react'
import {Container,Image,Row,Col,ListGroup,Card} from 'react-bootstrap'
import Firebase from 'firebase'
import './post.css'
import {Link} from 'react-router-dom'
import $ from 'jquery'
function Post(props) {
	const [postData,setPostData]=useState({})
	useEffect(()=>{
       let ref = Firebase.database().ref("/");
       ref.on("value", snapshot => {
      const state = snapshot.val();
      setPostData(state);
       })
	},[])
  const handleLike=()=>{
    $("#like").css("color","red");
  }
  const handleShare=()=>{
    $("#share").css("color","green");
  }
  const backgroundColor=postData.color;
  console.log(backgroundColor)
  console.log(postData)
	return(<div>
          <ListGroup className="list">
         <ListGroup.Item onClick={handleLike} style={{cursor:"pointer"}}><i className="material-icons" id="like">favorite_border</i>Like</ListGroup.Item>
          <ListGroup.Item onClick={handleShare} style={{cursor:"pointer"}}><i className="material-icons" id="share">share</i>Share</ListGroup.Item>
          </ListGroup>    

            <Container className="post" style={{backgroundColor:backgroundColor}}>
             <h1>{postData.topic}</h1>
             <h3><small>by </small><b> {postData.name}</b></h3>
             <Image className="user-img" src={postData.profile} roundedCircle  />
             <article className="content">
             {postData.content}
             </article>
        </Container>   
        <Container fluid>
        <ListGroup  className="list-small-screen" horizontal>
          <ListGroup.Item><Link to=""><i className="material-icons">favorite_border</i></Link>Like</ListGroup.Item>
          <ListGroup.Item><Link to=""><i className="material-icons">share</i></Link>Share</ListGroup.Item>
          </ListGroup>
          </Container> 
        <Card className="user" >
        <Card.Title>Meet {postData.name}</Card.Title>
        <Card.Body>
        <Image className="card-img" src={postData.profile}/><br/>
        Skills:<br/>
        Contact @:<br/>
        About:
        </Card.Body>
        </Card>
        </div>
		)
}
export default Post